/* Story.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.story {
  padding: 50px 20px;
  text-align: center;
}

.history-title {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0px;
}

.vision-title {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  margin-top: 0px;
  margin-bottom: 40px;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.story-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.large-card {
  width: 90%;
  background: linear-gradient(90deg, #001f3f, #004080);
  border-radius: 10px;
  padding: 20px;
  margin: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
  text-align: left;
}

.small-cards-container {
  display: flex;
  justify-content: space-between;
  width: 93%;
  margin-top: 20px;
}

.small-card {
  width: 45%;
  background: linear-gradient(90deg, #001f3f, #004080);
  border-radius: 10px;
  padding: 20px;
  margin: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
  text-align: left;
}

.story-image {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 10px;
}

.story-title {
  font-size: 24px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  margin-bottom: 10px;
}

.story-description {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  margin-bottom: 20px;
}
