/* Header.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.header-faq {
  padding-top: 120px;
  padding-bottom: 60px;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: white;
  text-align: center;
}

.header-content-faq {
  max-width: 100%;
  padding-left: 10%;
  padding-right: 10%;
}

.header-content-faq h1 {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  margin-bottom: 24px;
  text-align: center;
  color: #001f3f;
}

.header-description-faq {
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  margin-bottom: 0px;
  text-align: center;
  color: #001f3f;
}
