/* FAQ.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.faq-section {
  padding: 50px 150px;
  text-align: center;
  background: white;
}

.faq-title {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
  background: linear-gradient(90deg, #001f3f, #004080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 20px;
}

.faq-description {
  font-size: 16px;
  color: #333;
  margin-bottom: 40px;
  line-height: 1.5;
}

.faq-container {
  margin-bottom: 40px;
}

.faq-item {
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
  cursor: pointer;
}

.faq-question {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  color: #004080; /* Same blue as the title in the Installation component */
}

.faq-answer {
  margin-top: 10px;
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100, "YTLC" 500;
  color: #333;
  text-align: left;
}

.faq-button {
  background-color: #ffcc00;
  color: #004080;
  border: none;
  padding: 10px 20px;
  border-radius: 30px; /* Rounded corners like NavBar buttons */
  cursor: pointer;
  font-size: 16px;
  text-align: center;
  display: inline-block;
}

.faq-button:hover {
  opacity: 0.9;
}
