/* HeaderAboutUs.css */

@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

.header {
  display: flex;
  justify-content: space-between;
  padding: 120px;
  background: linear-gradient(90deg, #001f3f, #004080);
  color: white;
}

.header-content {
  max-width: 50%;
}

.header-content h1 {
  font-size: 36px;
  font-family: "Dosis", sans-serif;
  font-weight: 800;
  margin-bottom: 24px;
  text-align: left;
}

.header-description {
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  margin-bottom: 24px;
  text-align: left;
}

.highlight {
  font-weight: bold;
  color: #ffcc00;
}

.header-image img {
  max-width: 100%;
}
